@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Serif&display=swap);
@import url(https://fonts.googleapis.com/css?family=IM+Fell+DW+Pica+SC&display=swap);
@import url(https://fonts.googleapis.com/css?family=IM+Fell+DW+Pica+SC|Josefin+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Barriecito|IM+Fell+DW+Pica+SC|Josefin+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Barriecito&display=swap);
@import url(https://fonts.googleapis.com/css?family=Barriecito|Roboto+Mono&display=swap);


:root {
	--white:#fff;
	--light-grey:#f5f5f5;
	--grey:#d3d3d3;
	--dark-grey:#222;
	--dark-accent-grey:#111;
	--charcoal-grey:#2f2f2f;

	--light-pastel-purple:#b19cd9;
	--blue-magenta:#9921e8;
	--med-light-yellow:#e5d54f;
	--light-accent-blue:#eaedf0;

	/*--accent-gradient:linear-gradient(315deg, #9921e8 0%, #5f72be 74%);*/
	/*--accent-gradient:linear-gradient(315deg, #000 0%, #5f72be 74%);*/
	--accent-gradient:linear-gradient(315deg, #9921e8 0%, #000 74%);
	/*--collection-bg-gradient:linear-gradient(180deg, var(--blue-magenta) 0%, var(--light-pastel-purple) 74%);*/
	/*rgba(50,45,127,255)*/
	--cover-gradient:
		radial-gradient(ellipse at bottom left, transparent, rgba(50,45,127,255)),
        radial-gradient(ellipse at bottom left, transparent, rgba(50,45,127,255));
    --nav-gradient:
    	radial-gradient(at left bottom, rgba(50, 45, 127, 0.6), rgb(50, 45, 127)), 
    	radial-gradient(at left bottom, rgba(50, 45, 127, 0.6), rgb(50, 45, 127));
	--collection-bg-gradient:
		radial-gradient(ellipse at bottom left, transparent, #9921e8),
        radial-gradient(ellipse at bottom left, transparent, #b19cd9);
    --collection-bg-gradient-muted:
		radial-gradient(ellipse at bottom left, transparent, rgba(153, 33, 232, 0.5)),
        radial-gradient(ellipse at bottom left, transparent, rgba(177, 156, 217, 0.5));
	--fill-area-bg-color:#000;
	--main-area-bg-color:linear-gradient(
	 	to bottom,
	 	rgba(50, 45, 127, 1) 0%,
	 	rgba(90, 45, 127, 1) 20%,
	 	rgba(131, 47, 132, 1) 40%,
	 	rgba(174, 57, 135, 1) 50%,
	 	rgba(215, 68, 138, 1) 65%,
	 	rgba(233, 64, 44, 1) 75%,
	 	rgba(236, 95, 47, 1) 85%,
	 	rgba(238, 124, 50, 1) 91%,
	 	rgba(242, 163, 57, 1) 98%,
	 	rgba(248, 204, 64, 1) 100%
	);
	--end-retro-stripe-gradient:linear-gradient(
	 	to bottom,
	 	rgba(50, 45, 127, 1) 0%,
	 	rgba(90, 45, 127, 1) 60%,
	 	rgba(131, 47, 132, 1) 71%,
	 	rgba(174, 57, 135, 1) 74%,
	 	rgba(215, 68, 138, 1) 80%,
	 	rgba(233, 64, 44, 1) 87%,
	 	rgba(236, 95, 47, 1) 93%,
	 	rgba(238, 124, 50, 1) 96%,
	 	rgba(242, 163, 57, 1) 98%,
	 	rgba(248, 204, 64, 1) 100%
	);

	--standard-column-border:1px solid var(--charcoal-grey);
	--headline-padding:10px 0 10px 0;
	--content-box-shadow:5px 5px 36px 5px #000;
	--collection-gw-box-shadow:
		0 2.8px 2.2px rgba(0, 0, 0, 0.034),
		0 6.7px 5.3px rgba(0, 0, 0, 0.048),
		0 12.5px 10px rgba(0, 0, 0, 0.06),
		0 22.3px 17.9px rgba(0, 0, 0, 0.072),
		0 41.8px 33.4px rgba(0, 0, 0, 0.086),
		0 100px 80px rgba(0, 0, 0, 0.12);
}

html {
	background-color: #d3d3d3;
	background-color: var(--grey);
	height: 100%;
}

body {
	background: #d3d3d3 !important;
	background: var(--grey) !important;
	color: #222;
	color: var(--dark-grey);
	font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
	font-size: 1em;
	line-height: 1.3;
	margin: 0;
}









.fill-area  {
	bottom: -72px;
	/*background: var(--fill-area-bg-color);*/
	background: rgba(50,45,127,255);
	box-shadow: inset 3px 3px 5px -1px #111;
	pointer-events: none;
	position:fixed;
	padding:0;
	margin:0;
	top:0;
	left:0;
	width: 100%;
	height: 100%;
	transition: all 1s ease; 
	z-index: 2;
}

main {
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	align-items: center;
	width: 100vw;
	height: 100vh;
	background: var(--end-retro-stripe-gradient);
}
/* main::after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: repeating-linear-gradient(0deg, rgba(0, 0, 0, 0.15), rgba(0, 0, 0, 0.15) 1px, transparent 1px, transparent 2px);
	pointer-events: none;
}*/

.content {
	color: var(--light-accent-blue);
	display: flex;
	justify-content: center;
	align-items: center;
	flex-flow: row wrap;
	font-family: 'Droid Serif', serif;
	font-size: 1.3em;
	margin: 0 auto;
	height: 200px;
	width: 45%;
	z-index: 2;
	text-align: center;
	margin-top: 50px;
}

.slidecontainer {
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-flow: column;
	margin: 10vh auto;
	width: 80%;
	z-index: 2;
}

label {
	position: static;
	z-index: 1;
	font: 8rem "Monoton", sans-serif;
	/*color: rgba(51, 51, 51, 0.9);*/
	color: purple;
	margin: -5rem 0 2rem;
}
label::after {
  content: "%";
}

/* slider itself */
.slider {
	position: static;
	z-index: 2;
	width: 50vw;
	height: 1rem;
	background-color: purple;
	/*background-image: var(--accent-gradient);*/
	/*background: rgba(51, 51, 51, 0.9);*/
	-webkit-appearance: none; 
	-moz-appearance: none; 
	     appearance: none;
	border: none;
	outline: none;
	border-radius: 100vmax;
	box-shadow: inset 3px 3px 5px -1px #000;
}

.slider::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	width: 4vw;
	height: 4vw;
	background: var(--light-accent-blue);
	border-radius: 50%;
	cursor: pointer;
	box-shadow: 5px 5px 8px -1px #000;
	-webkit-transition: box-shadow .3s ease-in-out;
	transition: box-shadow .3s ease-in-out;
}

.slider::-moz-range-thumb {
	-moz-appearance: none;
	     appearance: none;
	width: 4vw;
	height: 4vw;
	background: var(--light-accent-blue);
	border-radius: 50%;
	cursor: pointer;
	box-shadow: 5px 5px 8px -1px #000;
	-moz-transition: box-shadow .3s ease-in-out;
	transition: box-shadow .3s ease-in-out;
}

/* ---------- @media ---------- */
@media screen and (max-width: 52em) {
	.content {
		height: 100px;
		width: 85%;
	}

	label {
		font: 4rem "Monoton", sans-serif;
		margin: -2rem 0 1rem;
	}
	.slider {
		width: 85vw;
	}
}

@media screen and (max-width: 33.250em) {
	.content {
		margin: 1em auto 0 auto;
	}
	.slidecontainer {
		margin: 3vh auto;
	}

	label {
		margin: 2rem 0 2rem;
	}

	.slider::-webkit-slider-thumb {
		width: 8vw;
		height: 8vw;
	}
}

















/* ---------- general/zine setup ---------- */
.magazine {
	background: #e5e5e5;
	display: grid;
	grid-template-rows: 130px 2fr 1fr;
	grid-template-columns: 1.5fr 1fr 1fr 1.5fr;
	height: 800px;
	/*height: 550px;*/
	max-width: 80em;
	outline: 6px solid #121219;
	width: 95vw;
	/*width: 80vw;*/
	/*margin: 140px auto auto auto;*/
	margin: 100px auto auto auto;
}

/* ---------- title ---------- */
.title {
	align-items: end;
	background: var(--dark-accent-grey);
	border-right: 1px solid var(--light-grey);
	border-bottom: 1px solid var(--light-grey);
	color: var(--grey);
	display: grid;
	font-family: 'Barriecito', cursive;
	font-weight: normal;
	font-size: calc(10px + 2.6vw);
	grid-row: 1;
	grid-column: 1 / 3;
	justify-content: end;
	padding-bottom: 20px;
	padding-right: 40px;
	text-transform: uppercase;
	z-index: 3;
}

/* ---------- images ---------- */
.sunimage {
	background-image: url(/cover-story/static/media/synthwave.5b2b607b.jpg);
	background-position: left center;
	background-repeat: no-repeat;
	background-size: cover;
	grid-row: 1 / 3;
	grid-column: 2 / 5;
	margin: 0 0 20px 20px;
	z-index: 2;
}

.drawing {
	/*display: none;*/

	background-image: url(/cover-story/static/media/mebygv.fa33ceea.png);
	background-position: bottom;
	background-repeat: no-repeat;
	background-size: 125%;

	grid-row: 2 / 4;
	grid-column: 1;
	margin: 0;
	padding: 0;

	z-index: 2;
}

/* ---------- quote ---------- */
.quote {
	align-self: end;
	background: rgba(0,0,0,0.5);
	border-left: 10px solid var(--med-light-yellow);
	bottom: 8%;
	color: var(--med-light-yellow);
	/*font-size: calc(12px + 1.1vw);*/
	font-family: 'Droid Serif', serif;
	font-size: 1.5em;
	grid-row: 2;
	grid-column: 4;
	justify-content: start;
	padding: 10px;
	position: relative;
	right: 125px;
	text-transform: uppercase;
	z-index: 3;
}

/* ---------- content ---------- */
.content1 {
	box-shadow: var(--content-box-shadow);
	display: block;
	grid-row: 2 / 4;
	grid-column: 1 / 2;
	text-align: left;
	z-index: 2; /* IE 9 */ /* Safari 3-8 */
	transform: rotate(-1deg);
}

.content2 {
	align-items: center;
	box-shadow: var(--content-box-shadow);
	display: grid;
	grid-row: 3 / 4;
	grid-column: 2 / 5;
	grid-template-columns: 1fr 1fr;
	grid-column-gap: 30px;
	text-align: right;
	z-index: 2; /* IE 9 */ /* Safari 3-8 */
	transform: rotate(1deg);
}

.content1, .content2 {
	background-color: #a9a9a9;
	font-family: 'Roboto Mono', monospace;
	font-size: 16px;
	/*overflow: scroll;*/
	padding: 20px 20px;
}
.content2 p {
	transform: rotate(-2deg);
}

.content1, 
.content2,
.content1 p,
.content2 p {
	border-radius: 0.125em;
}
.content1 h1, 
.content1 p,
.content2 p {
	background: #fff;
	color: var(--dark-grey);
	padding: 0.5%;
}

/* ---------- @collection ---------- */
.invite {
	background: var(--grey);
	border-bottom: 1px solid var(--dark-grey);
	border-right: 5px solid var(--dark-grey);
	color: var(--dark-accent-grey);
	display: flex;
	align-items: flex-end;
	justify-content: flex-end;
	/*font-size: calc(12px + 1.6vw);*/
	font-size: 1.95em;
	font-style: italic;
	font-weight: bold;
	grid-column: 4;
	grid-row: 3;
	height: 66%;
	padding-bottom: 20px;
	padding-right: 25px;
	text-align: right;
	text-decoration: none;
	text-transform: uppercase;
	width: 75%;
	z-index: 2;
}

.invite:hover {
	background-color: var(--med-light-yellow);
	transition: .7s;
}

.aboutemph {
	transform: scaleY(1.5);
	white-space: wrap;
}

.invite a {
	text-decoration: none;
	color: var(--dark-accent-grey);
}

/* ---------- @media ---------- */
@media only screen and (min-width: 240px) and (max-width: 479px) {

	.quote {
		left: 50px;
	}

	.drawing {
		background-size: 125%;
	}

}

@media only screen and (max-width: 73em) {
	.magazine {
		grid-template-rows: 120px 4fr 3fr 3fr 2fr 0.5fr;/*130px 2fr 1fr*/
		grid-template-columns: 95vw; /*1.5fr 1fr 1fr 1.5fr*/
		height: 1800px;
		width: 95vw;
	}

	.title {
		font-size: calc(10px + 3.6vw);
		grid-row: 1;
		/*z-index: 4;*/
	}

	.sunimage {
		background-size: auto;
		grid-row: 2;
		grid-column: 1;
		margin: 0;
	}

	.quote {
		bottom: 11%;
		font-size: calc(12px + 2.3vw);
		grid-row: 2;
		grid-column: 1;
		left: 75px;
		width: 66%;
	}

	.content1 h1, 
	.content1 p,
	.content2 p {
		padding: 2%;
	}

	.content1 {
		grid-row: 3;
		grid-column: 1;
		padding-top: 50px;
	}
	.content1 h1 {
		margin-bottom: 1em;
	}

	.content2 {
		grid-row: 4;
		grid-column: 1;
		padding-top: 50px;
		box-shadow: var(--content-box-shadow);
		display: block;
		background-image: radial-gradient(at top right #00ff00, gray);
		text-align: left;
		z-index: 2; /* IE 9 */ /* Safari 3-8 */
		transform: rotate(3deg);
	}

	.drawing {		
		display: block;

		background-image: url(/cover-story/static/media/mebygv.fa33ceea.png);
		background-position: auto;
		background-repeat: no-repeat;
		background-size: contain;

		grid-row: 5;
		grid-column: 1;
		margin: 0;
		padding: 0;
		margin-top: -10em;
		z-index: 2;
	}

	.invite {
		padding-bottom: 0.125%;
		padding-right: 25px;
		padding-top: 3%;

		color: var(--dark-accent-grey);
		display: flex;
		align-items: flex-end;
		justify-content: flex-end;
		font-size: calc(12px + 1.6vw);
		font-style: italic;
		font-weight: bold;
		grid-column: 1;
		grid-row: 6;
		height: 66%;
		margin: 0 auto;
		text-align: right;
		text-transform: uppercase;
		width: 70%;
		z-index: 3;
	}
}



/* ---------- general ---------- */

ul {
	list-style-type: none;
}

/* ---------- content ---------- */
.collection-content {
	display: grid;
	margin-top: -2em;
}
.collection-content p {
	margin: 1rem;
}

/* ---------- columns, general ----------*/
.columns {
	border-bottom: 10px solid var(--charcoal-grey);
	display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-template-rows: .75fr .75fr .25fr;
	margin-bottom: 30px;
}

.column {
	font-size: 1em;
	line-height: 20px;
	display: block;
	padding: 5% 3% 0 3%;
	vertical-align: top;
	margin-bottom: 50px;
	transition: all .7s;
}
.accent-column:hover {
	border-radius: 0.125em;
	box-shadow:var(--collection-gw-box-shadow);
}

/* ---------- columns, each ----------*/
.one {
	border-bottom: 1px solid rgba(50,45,127,255);
	font-size: 1.3em;
	grid-row: 1 / 3;
	grid-column: 1;
	line-height: 1.3;
	/*text-align: left;*/
}

.two {
	border-left: 1px solid rgba(50,45,127,255);
	border-right: 1px solid rgba(50,45,127,255);
	grid-row: 1 / 2;
	grid-column: 2;
	text-align: center;
}

.three {
	background:
		linear-gradient(
		  rgba(0, 0, 0, 0.2),
		  rgba(0, 0, 0, 0.2)
		);
	grid-row: 2;
	grid-column: 2 / 4;
	text-align: center;
}

.four {
	border-left: 1px solid rgba(50,45,127,255);
	grid-row: 1;
	grid-column: 4;
	text-align: center;
}

.five {
	grid-row: 1;
	grid-column: 3;
	text-align: center;
}

.six {
	grid-row: 3;
	grid-column: 1 / 4;
	padding: 1%;
}

.seven {
	border-top: 1px solid rgba(50,45,127,255);
	text-align: left;
	grid-row: 2;
	grid-column: 4;
	text-align: center;
}

.image {
	background-image: url(/cover-story/static/media/blondie.6623f4c5.png);
	background-position: right;
	background-repeat: no-repeat;
	background-size: contain;

	-webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
	filter: grayscale(100%);

	grid-row: 3;
	grid-column: 4;

	position: relative;
		height: 135%;
		bottom: 45px;
		right: 0;

	z-index: 3;
}

.speech-bubble {
	font-family: 'Roboto Mono', monospace;
	font-size: .9em;
	position: relative;
	background: var(--grey);
	border-radius: .4em;
	grid-row: 3;
	grid-column: 4;
	position: relative;
	bottom: 0;
	right: 10px;
	height: 50px;
	width: 50px;

	display: flex;
	  align-items: center;
	  justify-content: center;

	grid-column: 4;
	grid-row: 3 / 4;
	height: 25%;

	text-align: center;
	text-transform: uppercase;
	width: 40%;
	z-index: 2;
	position: relative;
	right: 145px;
	top: 60%;
	padding: 1%;
}

.speech-bubble:after {
	content: '';
	position: absolute;
	right: 0;
	top: 50%;
	width: 0;
	height: 0;
	border: 20px solid transparent;
	border-left-color: var(--grey);
	border-right: 0;
	border-top: 0;
	margin-top: -10px;
	margin-right: -20px;
}

/* ---------- columns, headlines ---------- */
.column .headline {
	text-align: center;
	line-height: normal;
	font-family: 'Playfair Display', serif;
	display: block;
	margin: 0 auto;
	max-width: 80%;
}

.column .headline.hl1 {
	font-weight: 700;
	font-size: 30px;
	text-transform: uppercase;
	padding: var(--headline-padding);
}

.column .headline.hl2 {
	font-weight: 400;
	font-style: italic;
	font-size: 24px;
	box-sizing: border-box;
	padding: var(--headline-padding);
}

.column .headline.hl2:before {
	border-top: var(--standard-column-border);
	content: '';
	width: 100px;
	height: 7px;
	display: block;
	margin: 0 auto;
}

.column .headline.hl2:after {
	border-bottom: var(--standard-column-border);
	content: '';
	width: 100px;
	height: 13px;
	display: block;
	margin: 0 auto;
}

.column .headline.hl3 {
	font-weight: 400;
	font-style: italic;
	font-size: 36px;
	box-sizing: border-box;
	padding: var(--headline-padding);
}

.column .headline.hl4 {
	font-weight: 700;
	font-size: 14px;
	box-sizing: border-box;
	padding: var(--headline-padding);
}

.column .headline.hl4:before {
	border-top: var(--standard-column-border);
	content: '';
	width: 100px;
	height: 7px;
	display: block;
	margin: 0 auto;
}

.column .headline.hl4:after {
	border-bottom: var(--standard-column-border);
	content: '';
	width: 100px;
	height: 10px;
	display: block;
	margin: 0 auto;
}

/* ---------- columns, particulars ----------*/
.links {
	display: flex;
		justify-content: center;
		align-items: center;
		flex-flow: row;
	font-style: italic;
}

.links a {
	color: inherit;
	padding: 5px;
	text-decoration: none;
}

.links a:hover {
	color: var(--blue-magenta);
	text-decoration: underline;
}

.column .citation {
	font-family: 'Playfair Display', serif;
	font-size: 36px;
	line-height: 44px;
	text-align: center;
	font-weight: 400;
	display: block;
	margin: 40px 0 40px 0;
	font-feature-settings: "liga", "dlig";
	max-width: 95%;
}

.column .citation:before {
	content: '';
	width: 100px;
	height: 16px;
	display: block;
	margin: 0 auto;
}

.column .citation:after {
	content: '';
	width: 100px;
	height: 16px;
	display: block;
	margin: 0 auto;
}

.column .figure {
	margin: 0 0 20px;
}

.column .figcaption {
	font-style: italic;
	font-size: 12px;
}

/* ---------- @media ---------- */

@media only screen and (max-width: 1300px) {
	/* ---------- fin ---------- */
	.six {
		padding: 0;
	}

	.column .citation {
		margin: 10px 0 10px 10px;
	}

	.speech-bubble {
		right: 120px;
		top: 75%;
	}
}


@media screen and (max-width: 64em) {
	.column .headline.hl4:before {
		border-top: 1px solid rgba(50,45,127,255);
	}

	.column .headline.hl4:after {
		border-bottom: 1px solid rgba(50,45,127,255);
	}

	.one {
		text-align: center;
	}
	.one > ul {
		padding-left: 0;
	}
}

@media only screen and (min-width: 600px) and (max-width: 1024px) {

	.hl1 {
		font-size: 22px;
	}

	.hl3 {
		font-size: 22px;
	}

	/* ---------- columns, general ----------*/
	.columns {
		grid-template-columns: 1.5fr 1fr 1fr 1.5fr;
		grid-template-rows: .5fr .5fr .5fr .5fr .40fr;
		grid-column-gap: 15px;
		margin-bottom: auto;
	}

	/* ---------- columns, each ----------*/
	.one {
		border-bottom: none;
		border-left: none;
